export default{
  data() {
    return {
      title: '',
      type: '',
      showDel: false,
      code_word: '获取验证码',
      username: '',
      code: '',
      showInfo: false
    }
  },
  onLoad(option) {
    this.type = option.type
    if(option.type === 'register') {
      this.title = '注册'
      return
    }
    if(option.type === 'forget') {
      this.title = '找回密码'
      return
    }
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    onInput(e) {
      let value = e.detail.value
      this.username = value
      if(value) {
        this.showDel = true
        return
      }
      this.showDel = false
    },
    goNext() {
      uni.navigateTo({
        url: 'password?type=' + this.type
      })
    },
    goLogin() {
      uni.navigateTo({
        url: 'login'
      })
    },
    getCode() {
      let value = /^1[3456789]\d{9}$/.test(this.username)
      console.log(this.username)
      if (!value){
        uni.showToast({
          title: '请输入正确的手机号码',
          icon: 'none'
        })
        return
      }
      this.code_word = 60
      this.showInfo = true
      setInterval(function() {
        if (this.code_word > 0) {
          this.code_word--
        }
        if (this.code_word === 0) {
          this.code_word = '获取验证码'
          this.showInfo = false
          return
        }
      }.bind(this), 1000)
    }
  }
}
